'use client'

import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay } from 'swiper/modules'
import 'swiper/css'
import 'swiper/css/autoplay'
import Image from 'next/image'

const sliderImages = [
  '/about.img/about1.jpg',
  '/about.img/about2.jpg',
  '/about.img/about3.jpg',
  '/about.img/about4.jpg',
  '/about.img/about5.jpg',
  
]

export default function AboutPage() {
  return (
    <main className=" px-6 py-10">
      <h1 className="text-center text-4xl font-bold text-black mb-10 bg-white/80 rounded-full py-2">About Us</h1>

      <section className="max-w-7xl mx-auto grid md:grid-cols-2 gap-10 items-center">
        {/* Left content */}
        <div className="space-y-5">
          <h2 className="text-3xl font-bold text-white">
            We are <span className="text-blue-600">Blue Lotus 360</span>
          </h2>
          <p className="text-white text-lg">
            Blue Lotus 360 delivers cutting-edge cloud ERP solutions, empowering businesses to
            streamline operations, innovate, and achieve sustainable growth.
          </p>
          <p className="text-white">
            Our expert team brings together technology and strategy to support your success in
            a rapidly evolving digital world.
          </p>
        </div>

        {/* Right image slider */}
        <div className="w-full h-96 relative rounded-lg overflow-hidden shadow-lg">
          <Swiper
            modules={[Autoplay]}
            autoplay={{ delay: 2500, disableOnInteraction: false }}
            loop={true}
            className="w-full h-full"
          >
            {sliderImages.map((src, idx) => (
              <SwiperSlide key={idx}>
                <Image
                  src={src}
                  alt={`Slide ${idx + 1}`}
                  layout="fill"
                  objectFit="cover"
                  className="rounded-lg"
                />
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </section>
    </main>
  )
}
