import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { FaBox, FaUsers, FaIndustry, FaFileInvoiceDollar, FaMoneyCheckAlt } from 'react-icons/fa'

type Tab = {
  label: string
  title: string
  description: string
  features: {
    label: string
    icon: React.ReactNode
  }[]
}

const tabs: Tab[] = [
  {
    label: 'Retailer',
    title: 'Retailer Dashboard',
    description:
      'Manage your retail store efficiently with our tailored dashboard and insightful analytics.',
    features: [
      { label: 'Inventory Tracking', icon: <FaBox /> },
      { label: 'Customer Insights', icon: <FaUsers /> },
      { label: 'Sales Reports', icon: <FaFileInvoiceDollar /> },
    ],
  },
  {
    label: 'Wholeseller',
    title: 'Wholeseller Portal',
    description:
      'Streamline your bulk orders and client management in one place.',
    features: [
      { label: 'Bulk Order Management', icon: <FaIndustry /> },
      { label: 'Client Accounts', icon: <FaUsers /> },
      { label: 'Invoice Generation', icon: <FaFileInvoiceDollar /> },
    ],
  },
  {
    label: 'Manufacturer',
    title: 'Manufacturer Suite',
    description:
      'Oversee production schedules and supply chain logistics seamlessly.',
    features: [
      { label: 'Production Planning', icon: <FaIndustry /> },
      { label: 'Supply Chain Monitoring', icon: <FaBox /> },
      { label: 'Quality Control', icon: <FaUsers /> },
    ],
  },
  {
    label: 'ERP',
    title: 'ERP System',
    description:
      'Integrate all your business processes with our robust ERP platform.',
    features: [
      { label: 'Financial Management', icon: <FaMoneyCheckAlt /> },
      { label: 'HR Management', icon: <FaUsers /> },
      { label: 'Resource Planning', icon: <FaBox /> },
    ],
  },
  {
    label: 'Payroll',
    title: 'Payroll Management',
    description:
      'Automate salary processing and employee records with ease.',
    features: [
      { label: 'Salary Calculations', icon: <FaMoneyCheckAlt /> },
      { label: 'Tax Deductions', icon: <FaFileInvoiceDollar /> },
      { label: 'Employee Records', icon: <FaUsers /> },
    ],
  },
  {
    label: 'IMSSpay',
    title: 'IMSSpay Gateway',
    description:
      'Secure and fast payment gateway integration for your business needs.',
    features: [
      { label: 'Payment Processing', icon: <FaMoneyCheckAlt /> },
      { label: 'Transaction History', icon: <FaFileInvoiceDollar /> },
      { label: 'Security Compliance', icon: <FaUsers /> },
    ],
  },
]

export default function TailwindTabsRightLeft() {
  const [selected, setSelected] = useState<Tab>(tabs[0])

  return (
    <div className="flex flex-col items-center justify-center gap-10 px-6 py-16 font-sans">
      {/* Tabs */}
      <div className="flex flex-wrap justify-center gap-4 max-w-5xl w-full">
        {tabs.map((tab) => {
          const isSelected = selected.label === tab.label
          return (
            <motion.button
              key={tab.label}
              onClick={() => setSelected(tab)}
              className={`
                relative
                px-5 py-2 rounded-lg
                font-semibold
                flex items-center gap-2
                transition-all duration-300
                cursor-pointer
                outline-none
                ${
                  isSelected
                    ? 'bg-blue-600 text-white border-2 border-blue-600 shadow-lg'
                    : 'bg-white text-blue-600 border-2 border-gray-300 shadow-sm hover:shadow-md'
                }
              `}
              whileHover={{ scale: 1.08 }}
            >
              {tab.label}
              {isSelected && (
                <motion.div
                  layoutId="underline"
                  className="absolute bottom-0 left-2 right-2 h-1 bg-white rounded"
                  transition={{ type: 'spring', stiffness: 500, damping: 30 }}
                />
              )}
            </motion.button>
          )
        })}
      </div>

      {/* Content Section with two columns: features left, text right */}
      <div className="max-w-5xl w-full flex flex-col md:flex-row items-center md:items-start justify-center gap-12">
        {/* Features Left */}
        <div className="grid grid-cols-[repeat(auto-fit,minmax(180px,1fr))] gap-6 justify-start flex-1 max-w-3xl">
          {selected.features.map((f) => (
            <motion.div
              key={f.label}
              className="bg-white rounded-xl p-6 shadow-md cursor-pointer select-none max-w-[220px] mx-auto flex flex-col items-center transition-transform duration-300 ease-in-out"
              whileHover={{ scale: 1.05, boxShadow: '0 8px 24px rgba(0, 112, 243, 0.2)' }}
              initial={{ scale: 1 }}
            >
              <div className="text-blue-600 mb-3 text-4xl drop-shadow-[0_2px_8px_rgba(0,112,243,0.3)]">
                {f.icon}
              </div>
              <div className="font-semibold">{f.label}</div>
            </motion.div>
          ))}
        </div>

        {/* Title & Description Right */}
        <div className="flex-1 max-w-xl text-right">
          <h2 className="text-3xl font-extrabold text-white mb-3">{selected.title}</h2>
          <p className="text-base leading-relaxed text-white px-4">{selected.description}</p>
        </div>
      </div>
    </div>
  )
}
