'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const clients = [
  { id: 1, name: 'Google', description: 'Global leader in search and cloud technology.' },
  { id: 2, name: 'Microsoft', description: 'Developer of Windows OS and Azure cloud.' },
  { id: 3, name: 'Amazon', description: 'E-commerce and cloud computing giant.' },
  { id: 4, name: 'Apple', description: 'Innovator in consumer electronics and software.' },
  { id: 5, name: 'Meta', description: 'Social media and metaverse company (Facebook).' },
  { id: 6, name: 'Netflix', description: 'Streaming entertainment platform worldwide.' },
  { id: 7, name: 'Tesla', description: 'Electric vehicles and renewable energy leader.' },
  { id: 8, name: 'Samsung', description: 'Global electronics and mobile manufacturer.' },
  { id: 9, name: 'IBM', description: 'Pioneer in enterprise IT and AI solutions.' },
  { id: 10, name: 'Intel', description: 'Leading chip manufacturer for PCs and servers.' },
  { id: 11, name: 'Adobe', description: 'Creators of Photoshop and digital design tools.' },
  { id: 12, name: 'Zoom', description: 'Video conferencing and communication tools.' },
  { id: 13, name: 'Slack', description: 'Team communication and productivity app.' },
  { id: 14, name: 'Oracle', description: 'Enterprise database and cloud systems.' },
  { id: 15, name: 'Spotify', description: 'Music streaming platform worldwide.' },
  { id: 16, name: 'Uber', description: 'Ride-sharing and food delivery technology.' },
  { id: 17, name: 'Airbnb', description: 'Vacation rentals and travel platform.' },
  { id: 18, name: 'LinkedIn', description: 'Professional networking platform by Microsoft.' },
  { id: 19, name: 'Dropbox', description: 'Cloud file storage and collaboration tools.' },
  { id: 20, name: 'Salesforce', description: 'CRM and customer experience leader.' },
  { id: 21, name: 'Shopify', description: 'E-commerce tools for online stores.' },
  { id: 22, name: 'PayPal', description: 'Secure online payment platform.' },
  { id: 23, name: 'Twitter', description: 'Social microblogging and news platform.' },
  { id: 24, name: 'YouTube', description: 'Video sharing and streaming giant by Google.' },
  { id: 25, name: 'GitHub', description: 'Code hosting and collaboration for developers.' },
  { id: 26, name: 'Trello', description: 'Task and project management tool.' },
  { id: 27, name: 'Asana', description: 'Team workflow and productivity platform.' },
  { id: 28, name: 'TikTok', description: 'Short-form video sharing and social media.' },
  { id: 29, name: 'Reddit', description: 'Community-driven forums and discussions.' },
  { id: 30, name: 'Baidu', description: 'Leading Chinese internet search provider.' },
  { id: 31, name: 'Alibaba', description: 'E-commerce and cloud technology in Asia.' },
  { id: 32, name: 'ZoomInfo', description: 'Sales and marketing intelligence data.' },
  { id: 33, name: 'Stripe', description: 'Online payment infrastructure for the web.' },
  { id: 34, name: 'Nvidia', description: 'Graphics and AI chip manufacturer.' },
  { id: 35, name: 'Dell', description: 'PC and IT infrastructure provider.' },
  { id: 36, name: 'HP', description: 'Computing and printing solutions.' },
  { id: 37, name: 'Lenovo', description: 'PC and mobile technology company.' },
  { id: 38, name: 'TCS', description: 'Global IT services and consulting (India).' },
  { id: 39, name: 'Infosys', description: 'Indian digital services and consulting firm.' },
  { id: 40, name: 'Wipro', description: 'IT consulting and business solutions.' },
];

export default function ClientsDiamondWithMotion() {
  const [selectedClient, setSelectedClient] = useState<typeof clients[0] | null>(null);
  const total = clients.length;
  const radius = 300;
  const center = 350;

  return (
    <section className="flex flex-col justify-center items-center  relative px-4 ">
      {/* Heading */}
      <motion.h2
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
        className="text-3xl font-bold text-center text-black bg-white/70 p-4 mb-6 rounded-full"
      >
        Our Valued Clients
      </motion.h2>

      {/* Diamond Container */}
      <div className="relative w-[700px] h-[700px]">
        <div className="absolute inset-0 transform rotate-45 rounded-xl" />

        {clients.map((client, i) => {
          const angle = (360 / total) * i;
          const rad = (angle * Math.PI) / 180;
          const x = center + radius * Math.cos(rad) - 30;
          const y = center + radius * Math.sin(rad) - 30;

          return (
            <motion.div
              key={client.id}
              onClick={() => setSelectedClient(client)}
              initial={{ scale: 0, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              transition={{ delay: i * 0.01, duration: 0.3 }}
              className="absolute w-[60px] h-[60px] bg-white dark:bg-gray-700 text-[10px] flex items-center justify-center text-center font-semibold text-gray-700 dark:text-gray-200 shadow-md rounded rotate-[-45deg] hover:bg-green-100 dark:hover:bg-green-800 cursor-pointer transition"
              style={{ top: `${y}px`, left: `${x}px` }}
            >
              {client.name}
            </motion.div>
          );
        })}
      </div>

      {/* Modal */}
      <AnimatePresence>
        {selectedClient && (
          <motion.div
            className="fixed inset-0 bg-black bg-opacity-50 backdrop-blur-sm flex justify-center items-center z-50"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-white dark:bg-gray-900 p-6 rounded-xl max-w-sm text-center shadow-xl"
              initial={{ scale: 0.8, y: -40, opacity: 0 }}
              animate={{ scale: 1, y: 0, opacity: 1 }}
              exit={{ scale: 0.8, y: -40, opacity: 0 }}
              transition={{ type: 'spring', stiffness: 300, damping: 20 }}
            >
              <h3 className="text-xl font-bold mb-2 text-gray-800 dark:text-white">
                {selectedClient.name}
              </h3>
              <p className="text-sm text-gray-600 dark:text-gray-300 mb-4">
                {selectedClient.description}
              </p>
              <button
                onClick={() => setSelectedClient(null)}
                className="mt-2 bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600 transition"
              >
                Close
              </button>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  );
}
